  gap: 6px;
}
.form-field.full-width {
  grid-column: 1 / -1;
}
.form-field label {
  font-size: 14px;
  font-weight: 500;
  color: #333;
}
.input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}
.password-input {
  padding-right: 44px !important;
}
.toggle-password-btn {
  position: absolute;
  right: 12px;
  background: none;
  border: none;
  color: #9ca3af;
  cursor: pointer;
  padding: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: color 0.2s;
  border-radius: 6px;
  width: 32px;
  height: 32px;
}
.toggle-password-btn:hover {
  color: #4285f4;
  background: rgba(66, 133, 244, 0.08);
}
.toggle-password-btn:active {
  transform: scale(0.95);
}
.eye-icon {
  width: 18px;
  height: 18px;
  stroke-linecap: round;
  stroke-linejoin: round;
}
.form-field input, .form-field select {
  padding: 10px 12px;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  font-size: 14px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  background: #f9fafb;
  font-family: inherit;
}
.form-field input:focus, .form-field select:focus {
  outline: none;
  border-color: #4285f4;
  background: white;
  box-shadow: 0 0 0 3px rgba(66, 133, 244, 0.1);
  transform: translateY(-1px);
}
.radio-group {
  display: flex;
  gap: 20px;
  margin-top: 8px;
}
.radio-option {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
}
.radio-option input[type="radio"] {
  width: 18px;
  height: 18px;
  margin: 0;
  cursor: pointer;
  accent-color: #4285f4;
}
.radio-option label {
  font-size: 14px;
  color: #333;
  cursor: pointer;
  margin: 0;
}
.preference-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 12px;
  margin-bottom: 14px;
}
.preference-card {
  padding: 14px;
  border: 2px solid #e5e7eb;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  background: #f9fafb;
}
.preference-card:hover {
  border-color: #4285f4;
  background: white;
  transform: translateY(-3px);
  box-shadow: 0 6px 16px rgba(66, 133, 244, 0.2);
}
.preference-card.selected {
  border-color: #4285f4;
  background: rgba(66, 133, 244, 0.05);
  animation: selectPop 0.3s ease-out;
}
@keyframes selectPop {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}
.preference-card h4 {
  font-size: 15px;
  font-weight: 600;
  color: #1a1a1a;
  margin: 0 0 6px 0;
}
.preference-card p {
  font-size: 13px;
  color: #666;
  margin: 0;
  line-height: 1.4;
}
.file-upload-area {
  border: 2px dashed #cbd5e1;
  border-radius: 10px;
  padding: 28px 18px;
  text-align: center;
  background: #f9fafb;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  margin-bottom: 14px;
}
.file-upload-area:hover {
  border-color: #4285f4;
  background: rgba(66, 133, 244, 0.02);
  transform: scale(1.02);
}
.file-upload-area.has-file {
  border-color: #4CAF50;
  background: rgba(76, 175, 80, 0.05);
  animation: fileSuccess 0.5s ease-out;
}
@keyframes fileSuccess {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}
.upload-icon {
  width: 40px;
  height: 40px;
  margin: 0 auto 12px;
  color: #4285f4;
}
.file-upload-area h4 {
  font-size: 15px;
  font-weight: 600;
  color: #1a1a1a;
  margin: 0 0 4px 0;
}
.file-upload-area p {
  font-size: 13px;
  color: #666;
  margin: 0;
}
.file-upload-input {
  display: none;
}
.file-name-display {
  margin-top: 12px;
  font-size: 14px;
  color: #4285f4;
  font-weight: 500;
}
.profile-preview {
  display: flex;
  gap: 32px;
  margin-bottom: 16px;
  flex: 1;
}
.profile-image-section {
  flex-shrink: 0;
}
.profile-image-circle {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background: linear-gradient(135deg, #e0e7ff 0%, #c7d2fe 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 12px;
  overflow: hidden;
  position: relative;
}
.profile-image-circle svg {
  width: 60px;
  height: 60px;
  color: #818cf8;
}
.profile-image-preview {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.upload-prompt {
  text-align: center;
}
.upload-prompt p {
  font-size: 12px;
  color: #666;
  margin: 4px 0;
}
.upload-prompt button {
  font-size: 13px;
  color: #4285f4;
  background: none;
  border: none;
  cursor: pointer;
  text-decoration: underline;
  padding: 4px;
}
.profile-details {
  flex: 1;
  display: flex;
  flex-direction: column;
}
.profile-details h4 {
  font-size: 16px;
  font-weight: 600;
  color: #1a1a1a;
  margin: 0 0 12px 0;
}
.detail-row {
  display: flex;
  justify-content: space-between;
  padding: 6px 0;
  border-bottom: 1px solid #f0f0f0;
}
.detail-row:last-child {
  border-bottom: none;
}
.detail-label {
  font-size: 13px;
  color: #666;
}
.detail-value {
  font-size: 13px;
  color: #1a1a1a;
  font-weight: 500;
  text-align: right;
}
.register-error {
  background: #fef2f2;
  border: 1px solid #fecaca;
  color: #dc2626;
  padding: 12px 16px;
  border-radius: 8px;
  font-size: 14px;
  margin-bottom: 20px;
}
.register-success {
  background: #f0fdf4;
  border: 1px solid #bbf7d0;
  color: #16a34a;
  padding: 12px 16px;
  border-radius: 8px;
  font-size: 14px;
  margin-bottom: 20px;
}
.form-actions {
  display: flex;
  gap: 12px;
  margin-top: 16px;
  padding-top: 12px;
  flex-shrink: 0;
}
.btn-back {
  flex: 1;
  padding: 12px;
  background: white;
  color: #666;
  border: 1px solid #e5e7eb;
  border-radius: 10px;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
.btn-back:hover {
  background: #f9fafb;
  border-color: #cbd5e1;
  transform: translateX(-3px);
}
.btn-back:active {
  transform: translateX(-1px);
}
.btn-next, .btn-submit {
  flex: 2;
  padding: 12px;
  background: linear-gradient(135deg, #4285f4 0%, #5a9ef8 100%);
  color: white;
  border: none;
  border-radius: 10px;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}
.btn-next::before, .btn-submit::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  transform: translate(-50%, -50%);
  transition: width 0.6s, height 0.6s;
}
.btn-next:hover::before, .btn-submit:hover::before {
  width: 300px;
  height: 300px;
}
.btn-next:hover, .btn-submit:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(66, 133, 244, 0.4);
}
.btn-next:active, .btn-submit:active {
  transform: translateY(0);
}
@media (max-width: 768px) {
  .register-card {
    padding: 24px;
  }
  .register-form-content {
    padding: 24px;
  }
  .form-grid {
    grid-template-columns: 1fr;
  }
  .step-navigation {
    gap: 8px;
  }
  .step-icon {
    width: 40px;
    height: 40px;
  }
  .step-nav-label {
    font-size: 11px;
  }
  .preference-grid {
    grid-template-columns: 1fr;
  }
  .profile-preview {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
}
:root {
  --background: #ffffff;
  --foreground: #171717;
  --color-background: var(--background);
  --color-foreground: var(--foreground);
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #ffffff;
    --foreground: #171717;
  }
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: 'Inter', 'Segoe UI', -apple-system, BlinkMacSystemFont, 'Helvetica Neue', Arial, sans-serif;
  margin: 0;
  padding: 0;
}
@keyframes pulse {
  50% {
    opacity: 0.5;
  }
}
@keyframes bounce {
  0%, 100% {
    transform: translateY(-25%);
    animation-timing-function: cubic-bezier(0.8, 0, 1, 1);
  }
  50% {
    transform: none;
    animation-timing-function: cubic-bezier(0, 0, 0.2, 1);
  }
}